.mainContainer {
  width: 800px;
  margin: 200px auto; }
  .mainContainer .searchTitle {
    color: #FFF;
    text-align: center;
    font-size: 40px; }
  .mainContainer .searchContainer {
    display: flex;
    text-align: left; }
  .mainContainer .buttonContainer {
    width: 20%; }
    .mainContainer .buttonContainer .button {
      background-color: #F52F41;
      border: none;
      border-radius: 5px;
      color: #FFF;
      height: 60px;
      font-size: 18px;
      cursor: pointer; }
  .mainContainer .addressContainer {
    width: 50%; }
    .mainContainer .addressContainer .address {
      position: relative; }
    .mainContainer .addressContainer .input {
      padding-left: 100px;
      border-radius: 5px 0px 0px 5px; }
  .mainContainer .typeContainer {
    width: 35%; }
    .mainContainer .typeContainer .input {
      border-radius: 0px 5px 5px 0px; }
  .mainContainer .inputContainer.address {
    width: 400px; }
  .mainContainer .inputContainer.type {
    width: 250px; }
  .mainContainer .inputContainer .input {
    width: 100%;
    height: 60px;
    border: none; }
